'use client';

import React, { useEffect, useMemo, useState } from 'react';
import { services } from '@/services/api';
import { AdminStats } from '@/services/api/adminStats.service';
import { formatCurrency, formatDate } from '@/utils/format';
import { RevenueByMonthChart } from '@/components/dashboard/RevenueByMonthChart';
import { SalesByStatusChart } from '@/components/dashboard/SalesByStatusChart';
import { Card } from '@/components/ui/card';
import { toast } from 'react-hot-toast';
import {
  ChartBarIcon,
  BuildingOfficeIcon,
  CurrencyDollarIcon,
  CalendarIcon,
  ClockIcon,
} from '@heroicons/react/24/outline';

const PERIOD_TO_MONTHS: Record<string, number> = {
  '7': 3,
  '30': 6,
  '90': 9,
  '365': 12,
};

export default function RelatoriosAnalytics() {
  const [stats, setStats] = useState<AdminStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [periodo, setPeriodo] = useState('90');

  useEffect(() => {
    const fetchData = async () => {
      try {
        setLoading(true);
        const response = await services.adminStats.getStats();
        setStats(response.data);
      } catch (error) {
        console.error(error);
        toast.error('Não foi possível carregar os relatórios.');
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  const filteredMonthlyRevenue = useMemo(() => {
    if (!stats) return [];
    const monthsToShow = PERIOD_TO_MONTHS[periodo] ?? 12;
    return stats.plans.monthlyRevenue.slice(-monthsToShow);
  }, [stats, periodo]);

  const pendingPurchases = stats?.plans.recentPurchases.filter((item) => item.status === 'PENDENTE') ?? [];

  if (loading || !stats) {
    return (
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
              Relatórios e Analytics
            </h1>
            <p className="text-gray-600 dark:text-gray-400">
              Consolidado financeiro das vendas de planos
            </p>
          </div>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          {[...Array(4)].map((_, idx) => (
            <div key={idx} className="h-32 animate-pulse rounded-xl bg-gray-200 dark:bg-gray-800" />
          ))}
        </div>
      </div>
    );
  }

  const { companies, plans, users } = stats;

  return (
    <div className="space-y-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900 dark:text-white">Relatórios e Analytics</h1>
          <p className="text-sm text-gray-600 dark:text-gray-400">
            Dados consolidados para acompanhar o desempenho do marketplace de planos.
          </p>
        </div>
        <div className="flex items-center gap-2">
          <label
            htmlFor="relatorios-periodo"
            className="text-sm text-gray-600 dark:text-gray-400 flex items-center gap-1"
          >
            <CalendarIcon className="h-4 w-4" /> Período:
          </label>
          <select
            value={periodo}
            onChange={(e: React.ChangeEvent<HTMLSelectElement>) => setPeriodo(e.target.value)}
            id="relatorios-periodo"
            className="rounded-lg border border-gray-300 bg-white px-3 py-2 text-sm text-gray-900 dark:border-gray-700 dark:bg-gray-800 dark:text-white"
          >
            <option value="7">Últimos 7 dias</option>
            <option value="30">Últimos 30 dias</option>
            <option value="90">Últimos 90 dias</option>
            <option value="365">Último ano</option>
          </select>
        </div>
      </div>

      {/* Cards principais */}
      <div className="grid grid-cols-1 md:grid-cols-2 xl:grid-cols-4 gap-6">
        <Card className="p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-500 dark:text-gray-400">Receita acumulada (planos)</p>
              <p className="mt-2 text-2xl font-semibold text-gray-900 dark:text-white">
                {formatCurrency(plans.totalRevenue)}
              </p>
            </div>
            <CurrencyDollarIcon className="h-8 w-8 text-emerald-500" />
          </div>
          <p className="mt-3 text-xs text-gray-500 dark:text-gray-400">
            {plans.paidPurchases} compras confirmadas desde o início.
          </p>
        </Card>
        <Card className="p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-500 dark:text-gray-400">Pendências de faturamento</p>
              <p className="mt-2 text-2xl font-semibold text-amber-600">
                {formatCurrency(plans.pendingRevenue)}
              </p>
            </div>
            <ClockIcon className="h-8 w-8 text-amber-500" />
          </div>
          <p className="mt-3 text-xs text-gray-500 dark:text-gray-400">
            {plans.pendingPurchases} compras aguardando confirmação de pagamento.
          </p>
        </Card>
        <Card className="p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-500 dark:text-gray-400">Planos no catálogo</p>
              <p className="mt-2 text-2xl font-semibold text-gray-900 dark:text-white">{plans.catalog}</p>
            </div>
            <ChartBarIcon className="h-8 w-8 text-indigo-500" />
          </div>
          <p className="mt-3 text-xs text-gray-500 dark:text-gray-400">
            {plans.activePlans} ativos · {plans.inactivePlans} suspensos.
          </p>
        </Card>
        <Card className="p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm font-medium text-gray-500 dark:text-gray-400">Empresas ativas</p>
              <p className="mt-2 text-2xl font-semibold text-gray-900 dark:text-white">{companies.active}</p>
            </div>
            <BuildingOfficeIcon className="h-8 w-8 text-blue-500" />
          </div>
          <p className="mt-3 text-xs text-gray-500 dark:text-gray-400">
            {companies.expiring} vencem em até 7 dias · {companies.expired} vencidas.
          </p>
        </Card>
      </div>

      <div className="grid grid-cols-1 xl:grid-cols-5 gap-6">
        <div className="xl:col-span-3">
          <RevenueByMonthChart
            data={filteredMonthlyRevenue.map((item) => ({
              label: item.label.toUpperCase(),
              revenue: item.revenue,
            }))}
          />
          <div className="mt-4 overflow-hidden rounded-xl border border-gray-200 bg-white dark:border-gray-800 dark:bg-white/[0.02]">
            <table className="min-w-full text-sm text-gray-700 dark:text-gray-300">
              <thead className="bg-gray-50 dark:bg-white/[0.05]">
                <tr>
                  <th className="px-4 py-2 text-left font-semibold">Mês</th>
                  <th className="px-4 py-2 text-left font-semibold">Receita</th>
                  <th className="px-4 py-2 text-left font-semibold">Transações</th>
                  <th className="px-4 py-2 text-left font-semibold">Ticket médio</th>
                </tr>
              </thead>
              <tbody>
                {filteredMonthlyRevenue.map((item) => (
                  <tr key={item.key} className="border-t border-gray-200 dark:border-gray-800">
                    <td className="px-4 py-2">{item.label.toUpperCase()}</td>
                    <td className="px-4 py-2">{formatCurrency(item.revenue)}</td>
                    <td className="px-4 py-2">{item.transactions}</td>
                    <td className="px-4 py-2">
                      {item.transactions ? formatCurrency(item.revenue / item.transactions) : '—'}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
        <div className="xl:col-span-2 space-y-6">
          <SalesByStatusChart items={plans.statusDistribution.map((item) => ({ status: item.status, total: item.total }))} />
          <Card className="p-6">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Empresas com mais clientes</h3>
            <div className="space-y-3">
              {companies.topByClients.map((empresa) => (
                <div key={empresa.nome} className="flex items-center justify-between text-sm">
                  <span className="text-gray-700 dark:text-gray-300">{empresa.nome}</span>
                  <span className="font-medium text-gray-900 dark:text-white">{empresa.clientes} clientes</span>
                </div>
              ))}
            </div>
          </Card>
        </div>
      </div>

      <div className="grid grid-cols-1 xl:grid-cols-2 gap-6">
        <Card className="p-6">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Planos que mais faturaram</h3>
          <div className="space-y-3">
            {plans.topPlans.map((plano) => (
              <div
                key={plano.nome}
                className="flex items-center justify-between rounded-lg border border-gray-200 bg-gray-50 px-4 py-3 dark:border-gray-800 dark:bg-white/[0.02]"
              >
                <div>
                  <p className="font-medium text-gray-900 dark:text-white">{plano.nome}</p>
                  <p className="text-xs text-gray-500 dark:text-gray-400">
                    {plano.purchases} vendas · Ticket médio {formatCurrency(plano.averageTicket)}
                  </p>
                </div>
                <p className="text-sm font-semibold text-gray-900 dark:text-white">
                  {formatCurrency(plano.revenue)}
                </p>
              </div>
            ))}
          </div>
        </Card>
        <Card className="p-6">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Empresas que mais compram</h3>
          <div className="space-y-3">
            {plans.topCompanies.map((empresa) => (
              <div
                key={empresa.nome}
                className="flex items-center justify-between rounded-lg border border-gray-200 bg-gray-50 px-4 py-3 dark:border-gray-800 dark:bg-white/[0.02]"
              >
                <div>
                  <p className="font-medium text-gray-900 dark:text-white">{empresa.nome}</p>
                  <p className="text-xs text-gray-500 dark:text-gray-400">
                    {empresa.purchases} compras
                  </p>
                </div>
                <p className="text-sm font-semibold text-gray-900 dark:text-white">
                  {formatCurrency(empresa.revenue)}
                </p>
              </div>
            ))}
          </div>
        </Card>
      </div>

      <div className="grid grid-cols-1 xl:grid-cols-2 gap-6">
        <Card className="p-6">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Pendências recentes</h3>
          {pendingPurchases.length === 0 ? (
            <p className="text-sm text-gray-500 dark:text-gray-400">Nenhuma compra pendente no período.</p>
          ) : (
            <div className="overflow-x-auto">
              <table className="min-w-full text-sm text-gray-700 dark:text-gray-300">
                <thead className="bg-gray-50 dark:bg-white/[0.05]">
                  <tr>
                    <th className="px-3 py-2 text-left font-semibold">Empresa</th>
                    <th className="px-3 py-2 text-left font-semibold">Plano</th>
                    <th className="px-3 py-2 text-left font-semibold">Valor</th>
                    <th className="px-3 py-2 text-left font-semibold">Criado em</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200 dark:divide-gray-800">
                  {pendingPurchases.map((compra) => (
                    <tr key={compra.id}>
                      <td className="px-3 py-2">{compra.empresa}</td>
                      <td className="px-3 py-2">{compra.plano}</td>
                      <td className="px-3 py-2 font-medium text-amber-600">
                        {formatCurrency(compra.valor)}
                      </td>
                      <td className="px-3 py-2 text-gray-500">{formatDate(compra.createdAt)}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </Card>

        <Card className="p-6">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">Usuários por perfil</h3>
          <div className="grid grid-cols-2 gap-4 text-sm">
            {Object.entries(users.breakdown).map(([role, total]) => (
              <div key={role} className="rounded-lg border border-gray-200 bg-gray-50 px-4 py-3 dark:border-gray-800 dark:bg-white/[0.02]">
                <p className="text-xs uppercase text-gray-500 dark:text-gray-400">{role}</p>
                <p className="mt-1 text-xl font-semibold text-gray-900 dark:text-white">{total}</p>
              </div>
            ))}
          </div>
        </Card>
      </div>
    </div>
  );
}
